export default {
	data() {
		return {
			isLogin:false,
		}
	},
	methods: {
		onShow:function(){
			if(uni.getStorageSync('isLogin')=="yes"){
				this.isLogin=true;
			}
		},
		logout:function(){
			//清除缓存
			try{
				uni.setStorageSync('isLogin', 'no');
			}
			catch(e){console.log(e)}
			
			//页面效果
			this.isLogin=false;
		},
		login:function(){
			uni.navigateTo({
				url:"../login/login"
			})
		}
	}
}
